export class QueryForm {
  constructor(query = {}) {
    this.userId = query.userId
    this.name = query.name
    this.phone = query.phone
    this.privacy = query.privacy
    this.life = query.life
    this.sizeType = query.sizeType
    this.master = ["false", "true"].includes(query.master)
      ? JSON.parse(query.master)
      : null
    if (query.date) {
      this.date = query.date.split(",")
    } else {
      this.date = []
    }
  }
}
