<template>
  <div class="default-margin">
    <v-card class="default-padding">
      <v-btn @click.native="$router.push(`/picture/handle`)"></v-btn>
      <v-btn
        v-if="
          $enum.equal(
            $route.query.life,
            $enum.PictureLifeState,
            $enum.PictureLifeState.DISAPPEAR
          )
        "
        @click="del"
        color="error"
        :disabled="!selectList.length"
        style="margin-left: 12px"
        >
      </v-btn>
    </v-card>
    <v-card class="default-padding" style="margin-top: 12px;">
      <v-form>
        <v-row>
          <v-col cols="12" sm="6" md="4">
            <v-menu
              v-model="dateMenu"
              :close-on-content-click="false"
              max-width="290"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  :value="query.date.join(' ~ ')"
                  clearable
                  placeholder=""
                  readonly
                  dense
                  solo
                  :hide-details="true"
                  v-on="on"
                  @click:clear="query.date = []"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="query.date"
                range
                @change="dateMenu = false"
              ></v-date-picker>
            </v-menu>
          </v-col>
          <v-col cols="12" sm="6" md="4">
            <select-user v-model="query.userId"></select-user>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-text-field
              v-model="query.name"
              placeholder=""
              clearable
              dense
              solo
              :hide-details="true"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-select
              v-model="query.privacy"
              :items="$enum.toArray($enum.PrivacyState)"
              item-text="label"
              item-value="value"
              placeholder=""
              clearable
              dense
              solo
              :hide-details="true"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-select
              v-model="query.life"
              :items="$enum.toArray($enum.PictureLifeState)"
              item-text="label"
              item-value="value"
              placeholder=""
              clearable
              dense
              solo
              :hide-details="true"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-select
              v-model="query.master"
              :items="[
                { label: ``, value: true },
                { label: ``, value: false }
              ]"
              item-text="label"
              item-value="value"
              placeholder=""
              clearable
              dense
              solo
              :hide-details="true"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-text-field
              v-model="query.phone"
              placeholder=""
              clearable
              dense
              solo
              :hide-details="true"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="3" md="2">
            <v-select
              v-model="query.sizeType"
              :items="$enum.toArray($enum.SizeType)"
              item-text="label"
              item-value="value"
              placeholder=""
              clearable
              dense
              solo
              :hide-details="true"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="4" md="2">
            <v-btn @click="changePage(1)"></v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card>
    <v-card class="default-padding" style="margin-top: 12px;">
      <v-data-table
        v-model="selectList"
        :headers="tableHeader"
        :items="list"
        :loading="loading"
        item-key="id"
        :show-select="
          $enum.equal(
            $route.query.life,
            $enum.PictureLifeState,
            $enum.PictureLifeState.DISAPPEAR
          )
        "
        calculate-widths
        hide-default-footer
        disable-pagination
        disable-filtering
        disable-sort
      >
        <template v-slot:item.url="{ item }">
          <a :href="$img.secdra(item.url)" target="_blank">
            <img
              v-lazy="$img.secdra(item.url, 'specifiedWidth')"
              style="margin: 10px 0;width: 250px"
            />
          </a>
        </template>
        <template v-slot:item.name="{ item }">
          <a :href="`${$config.domain}/picture/` + item.id" target="_blank">
            {{ item.name }}
          </a>
        </template>
        <template v-slot:item.privacy="{ item }">
          {{ $enum.PrivacyState[item.privacy] }}
        </template>
        <template v-slot:item.life="{ item }">
          {{ $enum.PictureLifeState[item.life] }}
        </template>
        <template v-slot:item.createDate="{ item }">
          {{ item.createDate | date }}
        </template>
        <template v-slot:item.userName="{ item }">
          <a :href="`${$config.domain}/user/` + item.user.id" target="_blank">
            {{ item.user.name }}
          </a>
        </template>
        <template v-slot:item.operate="{ item }">
          <v-btn small @click="listBySuit(item.id)" class="operate-btn">
            
          </v-btn>
          <template
            v-if="
              $enum.equal(
                item.life,
                $enum.PictureLifeState,
                $enum.PictureLifeState.EXIST
              )
            "
          >
            <v-btn
              @click="updatePrivacy(item.id, item.privacy)"
              color="primary"
              small
              class="operate-btn"
            >
              {{ opposite(item.privacy) }}
            </v-btn>
            <v-btn
              @click="remove(item.id, item.life)"
              color="warning"
              small
              class="operate-btn"
            >
              
            </v-btn>
          </template>
          <template
            v-if="
              $enum.equal(
                item.life,
                $enum.PictureLifeState,
                $enum.PictureLifeState.DISAPPEAR
              )
            "
          >
            <v-btn
              @click="reduction(item.id, item.life)"
              color="success"
              small
              class="operate-btn"
            >
              
            </v-btn>
          </template>
        </template>
      </v-data-table>
      <v-pagination
        v-model="pageable.page"
        :length="totalPages"
        :total-visible="7"
        @input="changePage"
      ></v-pagination>
    </v-card>
    <v-dialog v-model="drawerVisible">
      <v-data-table
        :headers="tableHeader"
        :items="suitList"
        :loading="suitLoading"
        calculate-widths
        hide-default-footer
        disable-pagination
        disable-filtering
        disable-sort
      >
        <template v-slot:item.url="{ item }">
          <a :href="$img.secdra(item.url)" target="_blank">
            <img
              v-lazy="$img.secdra(item.url, 'specifiedWidth')"
              style="margin: 10px 0;width: 250px"
            />
          </a>
        </template>
        <template v-slot:item.name="{ item }">
          <a :href="`${$config.domain}/picture/` + item.id" target="_blank">
            {{ item.name }}
          </a>
        </template>
        <template v-slot:item.privacy="{ item }">
          {{ $enum.PrivacyState[item.privacy] }}
        </template>
        <template v-slot:item.life="{ item }">
          {{ $enum.PictureLifeState[item.life] }}
        </template>
        <template v-slot:item.createDate="{ item }">
          {{ item.createDate | date }}
        </template>
        <template v-slot:item.userName="{ item }">
          <a :href="`${$config.domain}/user/` + item.user.id" target="_blank">
            {{ item.user.name }}
          </a>
        </template>
        <template v-slot:item.operate="{ item }">
          <template
            v-if="
              $enum.equal(
                item.life,
                $enum.PictureLifeState,
                $enum.PictureLifeState.EXIST
              )
            "
          >
            <v-btn
              @click="updatePrivacy(item.id, item.privacy)"
              color="primary"
              small
              class="operate-btn"
            >
              {{ opposite(item.privacy) }}
            </v-btn>
            <v-btn
              @click="remove(item.id, item.life)"
              color="warning"
              small
              class="operate-btn"
            >
              
            </v-btn>
          </template>
          <template
            v-if="
              $enum.equal(
                item.life,
                $enum.PictureLifeState,
                $enum.PictureLifeState.DISAPPEAR
              )
            "
          >
            <v-btn
              @click="reduction(item.id, item.life)"
              color="success"
              small
              class="operate-btn"
            >
              
            </v-btn>
          </template>
        </template>
      </v-data-table>
    </v-dialog>
  </div>
</template>

<script>
import { QueryForm } from "./script/model"
import { Pageable } from "../../assets/script/model"
import { mapActions } from "vuex"
import qs from "qs"
import { tableHeadersConstant } from "./script/constant"
import SelectUser from "./components/SelectUser"

export default {
  components: {
    SelectUser
  },
  data() {
    return {
      dateMenu: false,
      pageable: new Pageable(),
      query: null,
      tableHeader: tableHeadersConstant(),
      list: [],
      loading: false,
      totalPages: 0,
      userList: [],
      selectList: [],
      drawerVisible: false,
      suitLoading: false,
      suitList: []
    }
  },
  watch: {
    $route: {
      handler(to) {
        this.pageable.page = parseInt(to.params.page) || 1
        this.pageable.sort = "createDate,desc"
        this.query = new QueryForm(to.query)
        this.paging()
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions("picture", [
      "APaging",
      "AListBySuit",
      "AUpdatePrivacy",
      "ABatchRemove",
      "ABatchDelete",
      "AReduction"
    ]),
    changePage(page) {
      let query = Object.assign({}, this.query)
      query.date = query.date ? query.date.join(",") : []
      this.$router.push(`/picture/${page}?${qs.stringify(query)}`)
    },
    async paging() {
      this.loading = true
      this.$vuetify.goTo("#app")
      let paging = (
        await this.APaging(Object.assign({}, this.pageable, this.query))
      ).data
      this.loading = false
      this.list = paging.content || []
      this.totalPages = paging.totalPages || 0
      document.getElementsByTagName("main")[0].scrollTo(0, 0)
    },
    async listBySuit(id) {
      let row = this.list.find((item) => item.id === id)
      try {
        var pixivId = row.url.split("_")[0]
        this.suitLoading = true
        this.suitList = (await this.AListBySuit({ pixivId })).data
        this.suitLoading = false
        this.drawerVisible = true
      } catch (e) {
        this.$notify({ text: "pixiv", color: "info" })
      }
    },
    async updatePrivacy(id, privacy) {
      const result = await this.AUpdatePrivacy({ id })
      if (result.status === 200) {
        this.$notify({ text: "", color: "success" })
        this.changeState(
          id,
          "privacy",
          this.$enum.getKey(this.$enum.PrivacyState, this.opposite(privacy))
        )
      } else {
        this.$notify({ text: "", color: "error" })
      }
    },
    async remove(id, life) {
      const result = await this.ABatchRemove({ idList: [id] })
      if (result.status === 200) {
        this.$notify({ text: "", color: "success" })
        this.changeState(
          id,
          "life",
          this.$enum.getKey(
            this.$enum.PictureLifeState,
            this.oppositeLife(life)
          )
        )
      } else {
        this.$notify({ text: "", color: "error" })
      }
    },
    async reduction(id, life) {
      const result = await this.AReduction({ idList: id })
      if (result.status === 200) {
        this.$notify({ text: "", color: "success" })
        this.changeState(
          id,
          "life",
          this.$enum.getKey(
            this.$enum.PictureLifeState,
            this.oppositeLife(life)
          )
        )
      } else {
        this.$notify({ text: "", color: "error" })
      }
    },
    async del() {
      try {
        await this.$confirm({ text: "?" })
        const result = await this.ABatchDelete({
          idList: this.selectList.map((item) => item.id)
        })
        if (result.status === 200) {
          this.$notify({ text: "", color: "success" })
          this.changePage(1)
        } else {
          this.$notify({ text: "", color: "error" })
        }
        // eslint-disable-next-line no-empty
      } catch (e) {}
    },
    changeState(id, key, value) {
      let row = this.list.find((item) => item.id === id)
      if (row) {
        row[key] = value
      }
      let suitRow = this.suitList.find((item) => item.id === id)
      if (suitRow) {
        suitRow[key] = value
      }
    },
    opposite(privacy) {
      if (
        this.$enum.equal(
          privacy,
          this.$enum.PrivacyState,
          this.$enum.PrivacyState.PRIVATE
        )
      ) {
        return this.$enum.PrivacyState.PUBLIC
      } else {
        return this.$enum.PrivacyState.PRIVATE
      }
    },
    oppositeLife(life) {
      if (
        this.$enum.equal(
          life,
          this.$enum.PictureLifeState,
          this.$enum.PictureLifeState.EXIST
        )
      ) {
        return this.$enum.PictureLifeState.DISAPPEAR
      } else {
        return this.$enum.PictureLifeState.EXIST
      }
    },
    changeUserId(id) {
      let phone = null
      try {
        phone = this.userList.find((item) => (item.id = id)).phone
        // eslint-disable-next-line no-empty
      } catch (e) {}
      this.query.phone = phone
    }
  }
}
</script>

<style type="text/less" lang="less" scoped>
.operate-btn {
  margin: 5px;
}
</style>
